<template>
    <div class="star" :class="starType">
        <span class="star-item" v-for="(itemClass,k) in itemClasses" :key="k" :class="itemClass" ></span>
    </div>
  <!-- 加入score 为4.2
    则4 对于4个亮星星
    0.2 四舍五入，不给星星
    如果大于0.5 ，就给半个亮星星
   -->
</template>

<script>
    const LENGTH = 5;
    //  定义常量，相当于星星
    const ON = 'on';
    const OFF = 'off';
    const HALF = 'half';
    export default {
        props:[
            'score',
            'size'
        ],
        computed:{
            starType(){
                return 'star-'+ this.size;
            },
            itemClasses(){
                //定义空数组
                let result = [];
                //  取score的整数 
                let integer = Math.floor(this.score);
                //  取score的小数
                let decimal = this.score - integer;
                //判断循坏score的整数，添加到数组
                for(let i=0;i<integer;i++){
                    result.push(ON);
                }
                //如果小数部分大于0.5，就添加半个星星
                if(decimal > 0.5){
                    result.push(HALF);
                }
                //如果数组的长度小于5，当数组长度2星星为2时，执行3次，就添加3个buliang
                while(result.length < LENGTH){
                    result.push(OFF);
                }
                return result;
            }
        }
    }
</script>

<style lang="less" scoped>
    .star{
        &.star-small{
                .star-item{
                    display: inline-block;
                    width:0.3rem;
                    height: 0.3rem;
                    margin-right: 0.1rem;
                    &.on{
                        background-image: url('star_on.png');
                        background-size: 0.3rem 0.3rem;
                        background-repeat: no-repeat;
                    }
                    &.half{
                        background-image: url('star_half.png');
                        background-size: 0.3rem 0.3rem;
                        background-repeat: no-repeat;
                    }
                    &.off{
                        background-image: url('star_off.png');
                        background-size: 0.3rem 0.3rem;
                        background-repeat: no-repeat;
                    }
                }
            }
            .star-item{
                    display: inline-block;
                    width:0.48rem;
                    height: 0.48rem;
                    margin-right: 0.32rem;
                    &.on{
                        background-image: url('star_on.png');
                        background-size: 0.48rem 0.48rem;
                        background-repeat: no-repeat;
                    }
                    &.half{
                        background-image: url('star_half.png');
                        background-size: 0.48rem 0.48rem;
                        background-repeat: no-repeat;
                    }
                    &.off{
                        background-image: url('star_off.png');
                        background-size: 0.48rem 0.48rem;
                        background-repeat: no-repeat;
                    }
                }
        
        }

</style>




