<template>

  <div class="carDetail" v-show="showCarDetail">
        <div class="bg"  >
          <div class="bg2">

            
              <div class="bg3" @click='hideCar'>
                  <div class="top">
                      <p class="p1">购物车</p>
                      <p class="p2" @click="clearNum">清空</p>
                  </div>
                  <div class="foodName" v-for="(v,k) in foodList" :key="k" >
                      <p class="f">{{v.name}}</p>
                      <div class="carCom">
                            <p class="m1">￥{{v.price}}</p>
                            <div class="m2">
                              <carComponent :food='v'></carComponent>
                            </div>
                      </div>
                  </div>
              </div>
              

          </div>
        </div>
 
   </div>
</template>
<script>
import {mapGetters} from 'vuex'
import BScroll from 'better-scroll'
import carComponent from '../carComponent/carComponent.vue'
export default {
    props:[
      "foodList",// 从APP。vue中传递出来的食物列表
    ],
  computed:{
      ...mapGetters([
          'showCarDetail',
          'goods',
          'food'
      ])
  },
  components:{
      carComponent
  },
  methods:{
      hideCar(){
          this.$store.dispatch('hideCar');
      },
      clearNum(){//清空购物车
            this.foodList.forEach((food)=>{
                food.count=0;
            })
            this.$store.dispatch("clearNum");
      }
  },
 
  

}
</script>
<style lang="less" scoped>

 .carDetail{
      width: 100%;
        // height:100%;
        position:fixed;
        top:0;
        left:0;
       background-color: white;
       z-index: 6;
       bottom:1rem;
       
        .bg{
            width: 100%;
            height: 100%;
            background:rgba(7,17,27,0.8);
           
             position:fixed;
                top:0;
                left:0;
                
                .bg3{
                    background: white;
                    position: absolute;
                    left: 0;
                    bottom:0;
                    width:100%;
                    height:7rem;
                    .top{
                        width:100%;
                        height: 0.8rem;
                        background:#f3f5f7; 
                        border-bottom:0.01rem solid rgba(7,17,27,0.1);
                        display: flex;
                        justify-content: space-between;
                       
                        .p1{
                            font-size: 0.28rem;
                            font-weight: 200;
                            color: rgb(7,17,27);
                            line-height: 0.8rem;
                            margin:0rem 0 0.2rem 0.36rem;
                        }
                        .p2{
                            font-size: 0.24rem;
                           
                            color: rgb(0,160,220);
                            line-height: 0.8rem;
                            margin:0rem 0.36rem 0.2rem 0rem;
                        }

                    }
                    .foodName{
                        width: 100%;
                        height: 0.96rem;
                        line-height: 0.96rem;
                        margin:0.24rem 0.24rem 0.24rem 0.36rem;
                        border-bottom:0.01rem solid rgba(7,17,27,0.1);
                        display: flex;
                        justify-content: space-between;
                        f{
                            font-size: 0.28rem;
                            color: rgb(7,17,27);
                            line-height: 0.96rem;
                        }
                        .carCom{
                            display: flex;
                            justify-content: space-between;
                            margin-right:1.2rem;
                            .m1{
                                font-size: 0.28rem ;
                                font-weight: 700;
                                color:rgb(240,20,20);
                                line-height: 0.8rem;
                                
                            }
                            .m2{
                                margin-top:0.1rem;
                                width:1.5rem;
                                margin-left:0.4rem;
                            }
                        }
                    }
                }
              
                
                   
        }

 }


</style>





