<template>
<div class="run" id="run" ref="runs">
		<div class="foodDetail" v-show='detailShow'>
			<!-- <div class="bg"> -->
			<div class="goodsImg">
				<div class="foodImg" @click='hideDetail'>
						<img :src="food.image" alt="" >
				</div>
				<!-- 粥的价格及销售 -->
				<div class="goodsContent">
					<p class="goodsName"> {{food.name}}</p>
					<div class="sellerNum">
						<p class="monthSell">月售{{food.sellCount}}份</p>
						<p class="goodRates">好评率100%</p>
					</div>
					
					<div class="bught">
						<div class="goodsPrice">
							<p class="newPrice">￥</p><span>{{food.price}}</span>
							<p class="oldPrice">￥28</p>
						</div>
						<div class="addGoodsCar" @click="showCar" v-show="!showCars">加入购物车</div>
						<div class="carD" v-show="showCars" >
								<carComponent :food='food'></carComponent>
						</div>
					</div>
				</div>
			</div>
			<div class="m-bg"></div>

			<!-- 商品介绍 -->

			<div class="goodsDetail">
				<p class="goodsDetail-1">商品介绍</p>
				<p class="goodsDetail-2">{{food.info}}</p>
			</div>
			<div class="m-bg1"></div>
			
			
			
			<!-- 商品评价 -->
			<div class="btnContent">
				<p> 商品评价</p>
				<ratingSelect></ratingSelect>
				
			</div>

			<!-- 评价的内容(下面是1条评论内容，其他的可循环获取) -->

			<div class="rateContent" v-for="(v,i) in selectRatings" :key='i'>
				<div class="raterInfo">
						<div class="rateTime">
							{{v.rateTime | time}}
						</div>
						<div class="raterDetial">
								<div class="raterCount">
									{{v.username}}
								</div>
								<img :src="v.avatar" alt="">
						</div>
				</div>
				<div class="raterContent">
						<!-- <img src="../../assets/imgs/figer-on.png" alt=""> -->
						<p>{{v.text}}</p>
				</div>
				
			</div>
			

			<!-- </div> -->
			<div class="last"></div>

		</div>
</div>
</template>
<script>
import {mapGetters} from 'vuex'
import BScroll from 'better-scroll'
import carComponent from '../carComponent/carComponent.vue'
import ratingSelect from '../ratingSelect/ratingSelect.vue'
import time from '../../time.js'
export default {
	computed:{
		...mapGetters([
			'detailShow',
			'goods',
			'food',
			'ratings',
			'showCars',
			'selectRatings'
			
		])
	},
	components:{
		carComponent,
		ratingSelect
	},
	  watch:{
       goods(){
				
			}
    },
    mounted(){
            this.$nextTick( () => {
					if( !this.goodsSCroll ){
						this.goodsSCroll = new BScroll( document.getElementById('run') ,{
							click:true
						});
					}else{
						this.goodsSCroll.refresh();
					}
				});
				this.hideDetail();
			this.$store.dispatch('changTypes',['全部','推荐','吐槽']);
	},
	methods:{
		hideDetail(){
				this.$store.dispatch('hideDetail');
		},
		showCar(){
			this.$store.dispatch('showCar');
		}
		
		
		
	}
  
}
</script>
<style lang="less" scoped>
.run{
		position: absolute;
		top:3.6rem;
		bottom: 1rem;
      
        height: 6.35rem;
        overflow: hidden;


	.foodDetail{
		width: 100%;
		
		 z-index: 99999999999;
		position: fixed;
		left:0;
		top: 0;
		right:0;
		bottom:0;
		background: #fff;
		z-index: 9999999999;
		
				.foodImg{
				position: relative;
				width: 100%;
				height: 0;
				padding-top:100%;
					img{
						width:100%;
						height:100%;
						position: absolute;
						top: 0;
						left: 0;
					}
				}
				
				.goodsContent{
						margin:0.36rem;
						
						.goodsName{
							font-size: 0.28rem;
							font-weight: 700;
							color: rgb(7,17,27);
							line-height: 0.28rem;
							
							}
							.sellerNum{
								margin-top:0.16rem;
								display: flex;
								justify-content: flex-start;
								.goodRates{
									margin-left: 0.24rem;
								}
							}
							.bught{
								margin-top:0.36rem;
								display: flex;
								justify-content: space-between;
								.goodsPrice{
										display: flex;
										justify-content: flex-start;
										.newPrice{
											font-size: 0.2rem;
											font-weight:normal;
											color: rgb(240,20,20);
											line-height: 0.48rem;
										}
										span{
											font-size: 0.28rem;
											font-weight: 700;
											color: rgb(240,20,20);
											line-height: 0.48rem;
										}
										.oldPrice{
											font-size: 0.20rem;
											font-weight: 700;
											color: rgb(145,153,159);
											line-height: 0.48rem;
											text-decoration: line-through;
											margin-left: 0.24rem;
										}
								}
								.addGoodsCar{
									width: 1.48rem;
									height: 0.48rem;
									border-radius: 0.24rem;
									background-color: rgb(0,160,220);
									text-align: center;
									padding-top: 0.12rem;
									box-sizing: border-box;
									font-size: 0.20rem;
									font-weight: 700;
									color: rgb(255,255,255);
									line-height: 0.24rem;
								}
								.carD{
									position: absolute;
									top:8.9rem;
									right:0.2rem;
								}
							}
					}
				
				.m-bg{
						width: 100%;
						height: 0.36rem;
						background-color: #f3f5f7;
					}
		
		 /*商品介绍}*/
				.goodsDetail{
				
					padding: 0.36rem;
					border-top: 0.01rem solid rgba(7,17,27,0.5);
					border-bottom: 0.01rem solid rgba(7,17,27,0.5);
					.goodsDetail-1{
						font-size: 0.28rem;
						font-weight: 200;
						color: rgb(7,17,27);
						line-height: 0.28rem;
					}
					.goodsDetail-2{
						font-size: 0.24rem;
						font-weight: 200;
						color: rgb(77,85,93);
						line-height: 0.48rem;
						margin: 0.12rem 0.16rem 0 0.16rem;
					}
				}

				.m-bg1{
						width: 100%;
						height: 0.36rem;
						background-color: #f3f5f7;
					}

		 /*商品评价}*/
			.btnContent{
				border-bottom:0.01rem solid rgba(7,17,27,0.5);
				border-top:0.01rem solid rgba(7,17,27,0.5);
				background: rgb(255,255,255);	
							
				p{
					font-size: 0.28rem;
					font-weight: 200;
					color: rgb(7,17,27);
					line-height: 0.28rem;
					margin:0.36rem 0.36rem 0 0.36rem;
				}
				.commentComponents{
						

						// border: 0.01rem solid red;
				
					.btn{
						width: 90%;
						height:0.72rem ;
						margin-left:0.36rem;
						padding:0.36rem 0 0.36rem 0;
						display: flex;
						justify-content: flex-start; 
						border-bottom:0.01rem solid rgba(7,17,27,0.5);
								
									
						.btnAll{
							width: 1.5rem;
							height: 0.72rem;
							border-radius: 0.1rem;
							background-color: rgb(0,160,220);
							box-sizing: border-box;
							margin-bottom: 0.36rem;
							padding: 0.16rem 0.24rem 0.16rem 0.24rem;
						
							.b1{
								font-size: 0.24rem;
								color:rgb(255,255,255);
								line-height: 0.4rem;
							}
							.b2{
								font-size: 0.16rem;
								color:rgb(255,255,255);
								line-height: 0.32rem;
								margin-left: 0.15rem;
							}
						}
						.btnGood{
							width: 1.5rem;
							height: 0.72rem;
							border-radius: 0.1rem;
							background-color: lightblue;
							box-sizing: border-box;
							margin-left:0.16rem;
							padding: 0.16rem 0.24rem 0.16rem 0.24rem;
							
							.b1{
								font-size: 0.24rem;
								color:rgb(77,85,93);
								line-height: 0.4rem;
							}
							.b2{
								font-size: 0.16rem;
								color:rgb(77,85,93);
								line-height: 0.32rem;
								margin-left: 0.15rem;
							}
						
						}
						.btnNot{
							width: 1.7rem;
							height: 0.72rem;
							border-radius: 0.1rem;
							background-color: lightgray;
							box-sizing: border-box;
							padding: 0.16rem 0.24rem 0.16rem 0.24rem;
							margin-left:0.16rem;
							
							.b1{
								font-size: 0.24rem;
								color:rgb(77,85,93);
								line-height: 0.4rem;
							}
							.b2{
								font-size: 0.16rem;
								color:rgb(77,85,93);
								line-height: 0.32rem;
								margin-left: 0.15rem;
							}
						}
						}
					}
					
					.onlyContent{
						margin:0.36rem 0 0.36rem 0.36rem;
						img{
							width:0.4rem;
							height: 0.4rem;
							vertical-align:middle;
						}
						span{
							font-size: 0.24rem;
							color:rgb(147,153,159);
							line-height: 0.24rem;
							vertical-align:middle;
						}
					}
			}

	 /*评价的内容}*/
					.rateContent{
							padding:0.32rem 0.36rem 0.32rem 0.36rem;
							border-bottom: 0.01rem solid rgba(7,17,27,0.5);
							background: rgb(255,255,255);
								
							.raterInfo{
								display: flex;
								justify-content: space-between;
								.rateTime{
									font-size: 0.2rem;
									color: rgb(145,153,159);
									line-height: 0.24rem;
								}
								.raterDetial{
								display: flex;
								justify-content: space-between;
								.raterCount{
									font-size: 0.2rem;
									color: rgb(145,153,159);
									line-height: 0.24rem;
								}
								img{
									width: 0.24rem;
									height: 0.24rem;
									margin-left: 0.12rem;
								}	
							}
							}
							
							.raterContent{
								display: flex;
								justify-content: flex-start;
								margin-top:0.12rem;
								margin-bottom:0.32rem;
								img{
									width: 0.24rem;
									height: 0.24rem;
								}
								p{
									font-size: 0.24rem;
									color: rgb(7,17,27);
									line-height: 0.32rem;
									margin-left:0.08rem	
								}
							}
					}
					
					.last{
					width: 100%;
					height:1rem;
					background: white;
				
					}
				
			}
		

}

</style>





