<template>
   <div class="btnContent">

          <div class="btn">
                <!-- 全部  -->
              <div @click="select(2)" class="btnAll" :class="{'active':selectType === 2}" >
                {{types[0]}}<sub v-show="selectType === 2">{{selectRatings.length}}</sub>
              </div>
                <!-- 满意 -->
              <div @click="select(0)" class="btnGood" :class="{'active':selectType === 0}">
                 {{types[1]}}<sub v-show="selectType === 0">{{selectRatings.length}}</sub>
              </div>
                <!-- 不满意 -->
              <div @click="select(1)" class="btnNot" :class="{'active':selectType === 1}">
                 {{types[2]}}<sub v-show="selectType === 1">{{selectRatings.length}}</sub>
              </div>
          </div>
          
          <div @click="toggle" class="onlyContent" >
              <img src="../../assets/imgs/duigou.png" alt="" v-show="!haveCongtent">
              <img src="../../assets/imgs/dougou1.png" alt="" v-show="haveCongtent">
              <span :class="{'on':haveCongtent}">只看有内容的评价</span>
          </div>
      </div>
</template>
<script>
 import {mapGetters} from 'vuex'
export default {
    computed:{
        ...mapGetters([
            'selectType',
            'types',
            'selectRatings',
            'haveCongtent'
        ])
    },
    methods:{
        // 点击选择满意／不满意／或全部
        select(type){
            this.$store.dispatch('select',{type});
        },
        //点击选择只有内容的评价
        toggle(){
            // console.log(111);
            this.$store.dispatch('toggle');
        }
    },
    mounted(){
        this.$store.dispatch('changTypes',['全部','满意','不满意']);
    }
}
</script>

<style lang="less" scoped>
     /* 按钮*/
        .btnContent{
            border-bottom:0.01rem solid rgba(7,17,27,0.5);
            border-top:0.01rem solid rgba(7,17,27,0.5);
        
            .btn{
                width: 90%;
                height:0.72rem ;
                margin-left:0.36rem;
                padding:0.36rem 0 0.36rem 0;
                display: flex;
                justify-content: flex-start; 
                border-bottom:0.01rem solid rgba(7,17,27,0.5);
                .btnAll{
                    width: 1.5rem;
                    height: 0.72rem;
                     color:rgb(255,255,255);
                     text-align: center;
                    border-radius: 0.1rem;
                    background-color: rgb(0,160,220);
                    box-sizing: border-box;
                    margin-bottom: 0.36rem;
                    padding: 0.24rem 0.24rem 0.16rem 0.24rem;
                     &.active{
                             color:red;
                    }
           
                    
                }
                .btnGood{
                    width: 1.5rem;
                    height: 0.72rem;
                     color:rgb(255,255,255);
                     text-align: center;
                    border-radius: 0.1rem;
                    background-color: lightblue;
                    box-sizing: border-box;
                    margin-left: 0.16rem;
                    padding: 0.2rem 0.24rem 0.16rem 0.24rem;
                    &.active{
                                color:red;
                        }
        
                } 
                .btnNot{
                    width: 1.7rem;
                    height: 0.72rem;
                     color:rgb(255,255,255);
                     text-align: center;
                    border-radius: 0.1rem;
                    background-color: lightgray;
                    box-sizing: border-box;
                    padding: 0.2rem 0.24rem 0.16rem 0.24rem;
                    margin-left: 0.16rem;
                     &.active{
                                color:red;
                        }
                
                }
      
            }
           
            .onlyContent{
                margin:0.36rem 0 0.36rem 0.36rem;
              /*  border:0.01rem solid red;*/
                width: 2.5rem;
               
               
                img{
                    width:0.4rem;
                    height: 0.4rem;
                    vertical-align:middle;
                }
                span{
                    font-size: 0.24rem;
                    color:rgb(147,153,159);
                    line-height: 0.24rem;
                     vertical-align:middle;
                    &.on{
                    color:rgb(3,160,220);
                }
                }
                
            }
        
        }
</style>




