import axios from 'axios';
import TYPES from '../types.js'

const state = {
        seller:{},
        sellerShow:false,
}

const getters = {
    seller(state){
        return state.seller;
    },
    sellerShow(state){
        return state.sellerShow;
    }

}

const actions = {
        //提交获取seller数据的动作
        getSeller({ commit }){
            //发送ajax请求,需要配代理，已在config／index.js中配置 
            axios.get('data.json').then(res => {
                // console.log(res.data.seller);
                console.log(res);
                commit(TYPES.GET_SELLER,res.data.seller);
            })
        },
        //提交跳转到sellerDetail页面的动作
        showDetail({commit}){
            commit(TYPES.SHOW_DETAIL)
        },
        //提交取消跳转到sellerDetail页面的动作
        hideDetail({commit}){
            commit(TYPES.HIDE_DETAIL)
        }
}

const mutations = {
        //获取seller数据,getters 把此值return出,去其他页面通过。。。mapGetters获取
        [TYPES.GET_SELLER](state,data){
            state.seller = data;
        }, 
        //执行跳转到sellerDetail页面的动作
        [TYPES.SHOW_DETAIL](state){
             //getters 把此值return出去，sellerDetail页面的v－show接收，实现跳转
            state.sellerShow = true;
        },
        //执行取消跳转到sellerDetail页面的动作
        [TYPES.HIDE_DETAIL](state){
            state.sellerShow = false;
        }
}

export default {
    state,
    getters,
    actions,
    mutations
}


