<template>

	<div class="goods" >
		<!-- 左边菜单列表 -->
		<div class="goodsComtent" ref="goodlist">
			<div>
			<div class="menu">
				<ul>
					<li @click='selectMenu(i)' :class='{"current":currentIndex == i}' class="menuList" v-for="(v,i) in goods" :key='i'>
						<span class="text">{{v.name}}</span>
					</li>
					
					
				</ul>
			</div>
			</div>
		</div>

			<!-- 右边食物列表 -->
			<div class="foodContent" ref="foodlist">
				<div> <!-- 滚动至少需要一个空的div，不然菜谱只能出现一个，控制台还会报错 -->
				<div v-for="(item,k) in goods" :key='k' class="foodmenus">
					<div class="foodType">{{item.name}}</div>
					<div class="foodDetali" v-for="(food,i) in item.foods" :key='i' @click='showFoodDetail(food)'>
						<div class="foodImg">
							<img :src="food.image" alt="">
						</div>
						<div class="foodTitle">
							<p class="ft">{{food.name}}</p>
							<p class="foodStyle">{{food.description}}</p>
							<div class="foodSell">
									<p class="foodNum">{{food.sellCount}}份</p>
									<p class="foodRate"> 好评率100%</p>
							</div>
							<div class="foodPrice">
								<span class="PriceType">￥</span><span class="newPrice">{{food.price}}</span>
								<p class="oldPrice">￥28</p>
								<div class="car">
									<carComponent :food='food'></carComponent>
								</div>
							</div>
							
						</div>
						

					</div>
				</div>
				</div>

			</div>

			<foodDetail></foodDetail>


		</div>

</template>

<script>
import {mapGetters} from 'vuex'
import BScroll from 'better-scroll'
import foodDetail from '../foodDetail/foodDetail.vue'
import carComponent from '../carComponent/carComponent.vue'
export default {
	components:{
		foodDetail,
		carComponent
	},
	data(){
		return{
			heights:[],//用于存储每类食物的开始值和最后一个值
			scrollY:''
		}
	},
	computed:{
		...mapGetters([
			
			'goods'
		]),

		//实现左侧菜单和右侧食物详情的联动，
		//3.计算当前位置，先计算右侧食物详情，当前位置在哪一区间内 然后返回当前i,就是每类食物详情的第一个事物的索引，
		//给每个li动态绑定样式，如果等于i，就变颜色
		currentIndex(){
			for(let i=0;i< this.heights.length;i++){
				let heights1 = this.heights[i];
				let heights2 = this.heights[i+1];
				if( this.scrollY  >= heights1 && this.scrollY < heights2){
					return i;
				}
			}
			return 0;
		}
	},
	mounted(){
		this.$nextTick( () => {

				this.initBScroll();
				this.calcHeight();

			});

	},

	methods:{
			initBScroll(){
				// 右侧滚动
				if( !this.foodlistBScroll ){
					this.foodlistBScroll = new BScroll( this.$refs.foodlist,{
						click : true,
						probeType:3,//实时派发scroll事件

					})
				}else{
					this.foodlistBScroll.refresh();
				};


					//2.然后绑定一个滚动方法
				this.foodlistBScroll.on('scroll',(pos)=>{
					//pos
					// console.log(pos);
					//获取滑动的y坐标
					this.scrollY = Math.abs(Math.round(pos.y));
					// console.log(this.scrollY);

				})
				// 左侧滚动
				if( !this.goodlistBScroll ){
					this.goodlistBScroll = new BScroll( this.$refs.goodlist,{
						click : true

					})
				}else{
					this.goodlistBScroll.refresh();
				}
			},
			//1.先计算高度，添加到heights数组
			calcHeight(){
				//取到所有食物
					let foodMenu = document.getElementsByClassName("foodmenus");

					let height = 0;
					this.heights.push(height);
					for(let i = 0 ; i <foodMenu.length;i++){
						//获取每个（i）区块的高度
						height += foodMenu[i].clientHeight;
						this.heights.push(height);
					}
					// console.log(this.heights);
			},


			//4.左侧点击事件
			selectMenu(i){
					let foodMenu = document.getElementsByClassName("foodmenus");

					this.foodlistBScroll.scrollToElement(foodMenu[i],300);
			},
			

		//点击显示食物的详情页，把food传递出去，可以在goods。js中获取到
			showFoodDetail(f){
				this.$store.dispatch('showFoodDetail',f)
			},

	}
  
}
</script>

<style lang="less" scoped>

	.goods{
		position: absolute;
		display: flex;
		overflow: hidden;
		width: 100%;
		top:3.5rem;
		bottom:1rem;
		z-index: 5;
		/*左边菜单列表*/
		.menu{
			flex:0 0 1.6rem;
			width: 1.6rem;
			.menuList{
				width: 1.12rem;
				height: 1.08rem;
				font-size: 0.24rem;
				background: #f3f5f7;
				color: rgb(7,17,27);
				padding: 0 0.24rem;
				font-weight: 200;
				line-height: 0.28rem;
				display: table;
				&.current{
					background: cyan;
				}
				.text{
					width: 1.12rem;
					display: table-cell;
					vertical-align: middle;
					border-bottom:0.01rem solid rgba(7,17,27,0.1)
				}
			}
		}
		/*右边食物列表*/
		.foodContent{
			.foodType{
				width: 5.6rem;
				font-size: 0.24rem;
				color: rgb(147,153,159);
				line-height: 0.52rem;
				padding:0.2rem 0 0.2rem 0.28rem;
				background: #f3f5f7; 
				border-left:0.02rem solid #d9dde1;
			}
			.foodDetali{
				padding:0.36rem;
				/*border: 0.01rem solid red;*/
				display: flex;
				justify-content: flex-start;
				.foodImg{
					img{
						width: 1.2rem;
						height: 1.2rem;
					}
					
				}
				.foodTitle{
					.ft{
						font-size: 0.28rem;
						color: rgb(7,17,27);
						line-height: 0.28rem;
						margin:0.04rem 0 0 0.2rem;
					}
					.foodStyle{
						font-size: 0.2rem;
						color: rgb(147,153,159);
						line-height: 0.2rem;
						margin:0.16rem 0 0 0.2rem;
					}
					.foodSell{
						display: flex;
						justify-content: flex-start;
						margin:0.16rem 0 0 0.2rem;
						.foodNum{
							font-size: 0.2rem;
							color: rgb(147,153,159);
							line-height: 0.2rem;
						
						}
						.foodRate{
							font-size: 0.2rem;
							color: rgb(147,153,159);
							line-height: 0.2rem;
							margin-left:0.16rem;
						}
					}
					.foodPrice{
						display: flex;
						justify-content: flex-start;
						margin:0.16rem 0 0 0.2rem;
						.PriceType{
							font-size: 0.2rem;
							color: rgb(240,20,20);
							line-height: 0.48rem;
						}
						.newPrice{
							font-size: 0.28rem;
							color: rgb(240,20,20);
							line-height: 0.48rem;
						}
						.oldPrice{
							font-size: 0.2rem;
							color: rgb(147,153,159);
							line-height: 0.48rem;
							text-decoration: line-through;
						}
						.car{
							width: 1.5rem;
							height: 0.5rem;
							margin-left:1rem;
						}
					}
				}
				

			}
		}
	}


</style>





