<template>
<div class="run" id="run" ref="runs">
  <div class="sellerProfile">
      <!-- 商家接单数量 -->
        <div class="sellerOrder">
            <div class="sellerOrderDetail">
                <div class="sellerName">{{seller.name}}dfsg</div>
                <div class="sellerOrderNumber">
                    <myStar :score="3.6" :size='"small"'></myStar>
                    <p class="s1">(661)</p> 
                    <p class="s2">月售690单</p>
                </div>
                <div class="sellerCollect">
                    <img src="../../assets/imgs/heart.png" alt="">
                    <p>已收藏</p>
                </div>
            </div>
        </div>
        <!-- 商家送餐 详情 -->
        <div class="sellerSendDetail"> 
                    <div class="sellerPrice">
                        <p class="t1">起送价</p> 
                        <p class="tNum">{{seller.minPrice}}<span>元</span></p>      

                    </div>
                    <div class="sellerSendPrice">
                        <p class="t1">商家配送</p>
                        <p class="tNum">{{seller.deliveryPrice}}<span>元</span></p>
                    </div>
                    <div class="sellerSendTime">
                        <p class="t1">平均配送时间</p>
                        <p class="tNum">{{seller.deliveryTime}}<span> 分钟</span></p>
                    </div>
        </div>
        <div class="m-bg"></div>

        <!-- 公告与活动  -->
        <div class="sellerActive">
            <div class="sellerActiveDetail">
                <div class="sellerActiveDetail-1">
                    <p class="sellerActiveDetail-11">公告与活动</p>
                    <p class="sellerActiveDetail-12"> {{seller.bulletin}}</p>
                </div>
                <div class="sellerActiveDetail-2"  v-for="(v,k) in seller.supports" :key="k">
                    <img src="../../assets/imgs/decrease.png" alt="">
                    <p v-if="seller.supports">{{v.description}}</p>
                </div>
                
            </div>

        </div>
        <div class="m-bg1"></div>
        <!-- 商家实景 -->
        <div class="pics">
				<h1 class="title">商家实景</h1>
				<div class="pic-wrapper" ref='picwrap'>
					<ul class="pic-list" ref = 'piclist'>
						<li class="pic-item" v-for='(pic,k) in seller.pics' :key="k">
							<img :src="pic" width="120" height="90">
						</li>
					</ul>
				</div>
		</div>
        <!-- <div class="sellerView">
            <p>商家实景</p>
            <div class="sellerViewImg" ref='picwrap'>
                <ul class="pic-list" ref='piclist'>
                    <li class="pic-item" v-for='(pic,k) in seller.pics' :key="k">
                         <img :src="pic" alt="">
                    </li>
                </ul>
               
            </div>
        </div> -->

         <div class="m-bg2"></div>

         <!-- 商家信息 -->
         <div class="sellerInfos">
             <p>商家信息</p>
             <div class="sellerInfoDetail" v-for="(v,k) in seller.infos" :key="k">
                 <p>{{v}}</p>
             </div>

         </div>
         <div class="last"></div>

  </div>
</div>
</template>
<script>
import {mapGetters} from 'vuex'
import BScroll from 'better-scroll'
import myStar from '../star/star.vue'
export default {
    components:{
            myStar
    },
    computed:{
        ...mapGetters([
            'seller'
        ])
    },
    watch:{
       seller(){
			this._initPicture();	
			}
    },
    mounted(){
            this.$nextTick( () => {
            //实现滚动：内层容器的高度大于外层容器
					if( !this.sellerSCroll ){
						this.sellerSCroll = new BScroll( document.getElementById('run') ,{
                            click:true,//如果有点击事件，必须加上click：true，否则不会起效果
						});
					}else{
						this.sellerSCroll.refresh();
					}
				});
                this._initPicture();	
    },
    methods:{
        //定义一个私有方法
        _initPicture(){
           if(this.seller.pics){
               //图片宽度
               let pw = 120;
               //边框
               let m = 12;
               //总宽度
               let w = (pw + m)*this.seller.pics.length-m;
               //设置dom的宽度
               this.$refs.piclist.style.width = w + 'px';

               if(!this.picBScroll){
                   this.picBScroll = new BScroll(this.$refs.picwrap,{
                       //横向滚动
                       scrollX:true,
                       //
                       eventPassthrough:'vertical'
                   })
               }else{
                   this.picBScroll.refresh();
               }
           }
        }
    }
  
}
</script>
<style lang="less" scoped>
.run{
    position: absolute;
		top: 3.6rem;
		bottom: 1rem;
        z-index: -55;
        height: 8.5rem;
        overflow: hidden;
    .sellerProfile{
        width: 100%;
        margin-bottom:5rem;
        /* 商家接单数量 */
        .sellerOrder{
            
            margin:0 0.36rem 0 0.36rem;
            border-bottom: 0.01rem solid rgba(7,17,27,0.5);
            
            .sellerOrderDetail{
                width:100%;
                height: 0.88rem;
                margin:0.36rem 0 0.36rem 0;
               
                position: relative;
                z-index: -5;
                .sellerName{
                    font-size:0.28rem;
                    color:rgb(7,17,27);
                    line-height: 0.28rem;
                }
                .sellerOrderNumber{
                       
                        margin-top: 0.16rem;
                        display: flex;
                        justify-content: flex-start;
                        .s1{
                            font-size:0.2rem;
                            color:rgb(77,85,93);
                            line-height: 0.2rem;
                            margin-left:0.16rem;
                             margin-top: 0.08rem;
                        } 
                         .s2{
                             font-size:0.2rem;
                            color:rgb(77,85,93);
                            line-height: 0.2rem;
                            margin-left:0.24rem;
                             margin-top: 0.08rem;
                        } 
                }
                .sellerCollect{
                        
                        img{
                            width:0.48rem;
                            height:0.48rem;
                            position:absolute;
                            top:0;
                            right: 0;
                            z-index: -5;
                         
                        }
                        p{
                            font-size: 0.2rem;
                            color:rgb(77,85,93);
                            line-height: 0.2rem;
                            margin-top:0.56rem;
                            position: absolute;
                            top:0;
                            right:0;
                        } 
                }
            }
        }
        /* 商家送餐 详情*/
        .sellerSendDetail{
            width: 100%;
            height: 1.5rem;
            border-bottom:0.01rem solid rgba(7,17,27,0.5);
            display: flex;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-items: center;
            .sellerPrice , .sellerSendPrice,.sellerSendTime{
                width:2.5rem;
                height: 0.75rem;
                text-align: center;
                .t1{
                    font-size: 0.2rem;
                    color:rgb(147,153,159);
                    line-height: 0.2rem;
                    margin-bottom: 0.08rem;
                }
                
                .tNum{
                    font-size:0.48rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                    line-height: 0.48rem; 
                    span{
                        font-size:0.2rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: 0.2rem; 
                    }
                }

            }
            .sellerPrice,.sellerSendPrice{
                    border-right:0.01rem solid rgba(7,17,27,0.5);
                }
            
        }
        .m-bg{
            width: 100%;
            height: 0.36rem;
            background-color: #f3f5f7;
        }
        /* 公告与活动*/
        .sellerActive{
            width: 100%;
            height: 100%;
             border-bottom:0.01rem solid rgba(7,17,27,0.5);
             border-top:0.01rem solid rgba(7,17,27,0.5);
            .sellerActiveDetail{
                margin:0 0.36rem 0 0.36rem;
               
                .sellerActiveDetail-1{
                    border-bottom:0.01rem solid rgba(7,17,27,0.5);
                    .sellerActiveDetail-11{
                        font-size: 0.24rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: 0.24rem;
                        margin:0.36rem 0 0.16rem 0;
                    }
                    .sellerActiveDetail-12{
                        font-size: 0.24rem;
                        font-weight: 200;
                        color:rgb(240,20,20);
                        line-height: 0.48rem;
                        margin:0 0.24rem 0.32rem 0.24rem;
                    }

                }
                .sellerActiveDetail-2{
                    position: relative;
                    border-bottom:0.01rem solid rgba(7,17,27,0.5);
                    z-index: -10;
                    img{
                        z-index: -10;
                        width:0.35rem;
                        height: 0.35rem;
                        margin:0.32rem 0.12rem 0.32rem 0.24rem
                    }
                    p{
                      position: absolute;
                      top:0.34rem;
                      left: 0.68rem;
                      font-size: 0.24rem;
                      font-weight: 200;
                      color:rgb(7,17,27);
                        line-height: 0.32rem;

                    }

                }
            }
        }
        .m-bg1{
            width: 100%;
            height: 0.36rem;
            background-color: #f3f5f7;
        }

        /*商家实景
        .sellerView{
            width:100%;
           
            p{
                margin:0.36rem 0 0.24rem 0.36rem;
                font-size: 0.28rem;
                color: rgb(7,17,27);
                line-height: 0.28rem;
            }
            .sellerViewImg{
                white-space: nowrap;
                overflow: hidden;
               margin:0 0 0.36rem 0.36rem;
                img{
                    width: 2.4rem;
                    height: 1.8rem;
                    margin:0 0.12rem 0.36rem 0.36rem;
                     display:inline-block;
                }
            }
        }*/
        .pics{
			width: 100%;
			padding: 0.36rem 0 0.36rem 0.36rem;
			.title{
				padding-bottom: 0.24rem;
				font-size: 0.36rem;
			}
			.pic-wrapper{
				width: 7.1rem;
				white-space: nowrap;
				overflow: hidden;
				.pic-item{
					display: inline-block;
					margin-right: 0.12rem;
					&:last-child{
						margin: 0;
					}
				}
			}
		}
        .m-bg2{
            width: 100%;
            height: 0.36rem;
            background-color: #f3f5f7;
        }
        /*商家信息*/ 
        .sellerInfos{
            p{
                margin:0.36rem 0 0.24rem 0.36rem;
                font-size: 0.28rem;
                color: rgb(7,17,27);
                line-height: 0.28rem;  
            }
            .sellerInfoDetail{
                p{
                    font-size: 0.24rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                    line-height: 0.32rem; 
                    margin-right: 0.36rem;
                    padding:0.32rem 0.24rem 0 0.24rem;
                    border-top:0.01rem solid rgba(7,17,27,0.5);
                    box-sizing: border-box;
                }
            }
        }
        .last{
            height: 0.3rem;
        }
    }
}
</style>





