<template>
  <div id="app">
      
      
      <my-header></my-header>
       
      <div class="selects">
          <div class="selectGoods">
              <router-link to="/goods">商品</router-link>
          </div>
          <div class="selectRatings">
              <router-link to="/ratings">评价</router-link>
          </div>
          <div class="selectSeller">
            <router-link to="/seller">商家</router-link>
          </div>
      </div>

 
      <!-- 底部购物车 -->
      <div class="bott">
         <div class="left">
           <!-- car -->
           <div class="outCirle" @click="CarDetail">
              <div class="inCirle">
                  <!-- 如果总数大于0了，就让购物车上的数字显示 -->
                  <div class="foodNum" v-show='this.totalCount>0'>{{totalCount}}</div>
                  <img src="../src/assets/imgs/ car.png" alt="">
              </div>
           </div>
         </div>
          <div class="money" :class='{"heightlight":totalCount>0}'>¥{{totalPrice}}元</div>
         <div class="midd">
            另需配送费¥4元
         </div>
         <div class="right">
            <p>{{sendDesc}}</p>
         </div>
      </div>
      <CarDetail :foodList='selectFoods'></CarDetail>
    <router-view/>
  </div>
</template>

<script>
import header from './components/header/header.vue'
import {mapGetters} from 'vuex'
import CarDetail from './components/showCarDetail/showCarDetail.vue'
    export default {
      computed:{
        ...mapGetters([
          'goods',
          'seller'
        ]),

        //获得添加食物的数量
        selectFoods(){
          let foods = [];
          //  先遍历goods，即大的分类
          this.goods.forEach(good => {
            //然后在遍历goods里面的各种食物
            good.foods.forEach(food => {
              //如果食物的count大于0 ，就放进定义的foods数组中去
              if( food.count > 0 ){
                foods.push(food);
              }
            })
          });
          return foods;
        },

          //获取 foods里面的食物数量
        totalCount(){
            let count = 0;
            this.selectFoods.forEach( food => {
              count += food.count;
            });
            return count;
        },

        //获得选取食物应付的总金额
        totalPrice(){
          let total = 0;
          //对获取的数组进行遍历，里面的每种食物都有count
          this.selectFoods.forEach( food => {
            total += food.count * food.price
          });
          return total;
        },
      

        sendDesc(){
          if(this.totalPrice ===0){
            return `¥${this.seller.minPrice}元起送`;
          }else if(this.totalPrice < this.seller.minPrice){
            return `还差¥${this.seller.minPrice-this.totalPrice}元起送`;
          }else{
            return "去结算";
          }
        }

      },
    
      components:{
        "MyHeader":header,
        CarDetail
      },

      methods:{
        CarDetail(){
          console.log(111);
          this.$store.dispatch('CarDetail');
        }
      },
      mounted(){
        this.$store.dispatch('getSeller');
        this.$store.dispatch('getGoods');
        this.$store.dispatch('getRatings')
      }
          
    }
</script>

<style lang="less" scoped>
#app {
  /* z-index: -5;*/
  z-index: 5555555;
  .selects{
    width:100%;
    height: 0.8rem;
    font-size: 0.28rem;
    color:rgb(77,85,93);
    line-height: 0.8rem;
    display: flex;
    border-bottom: 0.01rem solid rgba(7,17,27,0.5);
    background:green;
     position: absolute;
    z-index: -555555555;
    top: 2.68rem;
    .selectGoods,.selectRatings,.selectSeller{
      flex:1;
      text-align: center;
      a{
        display: block;
        &.router-link-active{
          color:red;
        }
      }
    }
  }
 /* 底部购物车 ;*/
        .bott{
          width: 100%;
          height: 1.1rem;
          z-index: 66;
          position: absolute;
          left: 0;
          bottom: 0;
          background-color: #141d27;

          .left{
            position: absolute;
            left: 0.1rem;
            bottom: 0.02rem;
            
              
            .outCirle{
                  position: absolute;
                  left: 0.1rem;
                  bottom: 0.1rem;
                  width: 1.2rem;
                  height: 1.2rem;
                  border-radius: 1rem;
                  
                  background: #141d27;
                  .inCirle{
                      width: 1rem;
                      height: 1rem;
                      margin:0.12rem;
                      border-radius: 1rem;
                    
                      text-align: center;
                      background-color: rgba(255,255,255,0.4);
                      .foodNum{
                        position: absolute;
                        top:0rem;
                        left: 0.65rem;
                        width: 0.45rem;
                        height: 0.4rem;
                        font-size: 0.3rem;
                        text-align: center;
                        color:white;
                        border: 0.01rem solid red;
                        background-color: red;
                        border-radius: 0.4rem;
                        padding-top:0.1rem;
                        z-index:555 ;

                      }
                      
                      img{
                        position: absolute;
                        left: 0.34rem;
                        bottom:0.3rem;
                        width:0.5rem;
                        height: 0.5rem;
                        margin-top:0.28rem;
                      }
                  }

            }
          }
          
        }
        .money{
          width: 1.3rem;
          height:0.55rem;
          text-align: center;
            font-size: 0.32rem;
            color:rgba(255,255,255,0.4);
            font-weight: 700;
            line-height: 0.5rem;
            margin-left:1.4rem;
            margin-top:0.3rem;
            padding-top: 0.03rem;
            padding-right:0.1rem;
            border-right:0.01rem solid rgba(255,255,255,0.4);
            

          }
          .midd{
            position: absolute;
            left: 3rem;
            bottom:0.35rem;
            font-size: 0.24rem;
            color:rgba(255,255,255,0.4);
            font-weight: 700;
            line-height: 0.32rem;
          }
          .right{
            position: absolute;
            bottom:0;
            right: 0;
            width: 2rem;
            height: 1.1rem;
            
            border-left:0.01rem solid rgba(255,255,255,0.4);
            text-align: center;
            font-size: 0.32rem;
            color:rgba(255,255,255,0.4);
            font-weight: 700;
            line-height: 1.2rem;
           
           
         

          }
}
</style>



