import axios from 'axios';
import TYPES from '../types.js'


const state = {
    ratings:[],
    selectRatings:[],
    selectType:2,// 默认显示全部的评价
    types:['全部','满意','不满意'],
    haveCongtent:false
    
}

const getters = {
    ratings(state){
        return state.ratings;
    },
    selectRatings(state){
        return state.selectRatings;
    },
    selectType(state){
        return state.selectType;
    },
    types(state){
        return state.types;
    },
    haveCongtent(state){
        return state.haveCongtent;
    }
}

const actions = {
      //提交获取ratings数据的动作
      getRatings({ commit }){
        //发送ajax请求,需要配代理，已在config／index.js中配置 
        axios.get('data.json').then(res => {
            // console.log(res.data.ratings);
            console.log(res);
            commit(TYPES.GET_RATINGS,res.data.ratings);
        })
    },
        //点击筛选内容
        select({commit},{type}){
            commit(TYPES.SELECT,{type});
        },
        // 初始化状态
        changTypes({commit},_types){
            commit(TYPES.CHANGE_TYPES,_types);
        },
        // 只看有内容的评价
        toggle({commit}){
            commit(TYPES.TOGGLE);
        }

}

const mutations = {
    [TYPES.GET_RATINGS](state,data){
        state.ratings = data;
        //因评价做成插件，不能定义成死值，所以要初始化selectRatings数组，即把ratings的值给selectRatings数组
        state.selectRatings = data;
    }, 

    //选择查看全部／满意／不满意
    [TYPES.SELECT](state,{type}){
        //点击谁就把谁的状态改变一下，即点击谁就把它传过来的值赋值给selectType
        state.selectType = type;
        //	需要遍历的数据			所有的评价 24
        //过滤器
        state.selectRatings = state.ratings.filter(rating =>{
            // 全部//0 1 2  
            if(state.selectType === 2){//selectType=2, 显示全部的评价
                return true;
            }else{
                      //rateType＝0==>满意  当＝ 1==>不满意    
                return rating.rateType == state.selectType;
            }
        })
    },

    //只看有内容的评价
    [TYPES.TOGGLE](state){
        //如果现在选择的是只看内容的评价，那点击一下就是取消只看内容的评价
        state.haveCongtent = !state.haveCongtent;

        state.selectRatings = state.ratings.filter( rating =>{
			//   如果  我想看内容   但是  又没有内容  
			if( state.haveCongtent && !rating.text ){
				return false;
			}else if( state.selectType === 2 ){
				return true;
			}else{
				return rating.rateType == state.selectType;
			}
		})
    },

    [TYPES.CHANGE_TYPES](state,_types){
        state.types = _types;//改变types的内容，接受前面传递的数据，重新赋值
        //改变types后，也默认显示全部
        state.selectType = 2;
        //		需要遍历的数据	
        state.selectRatings = state.ratings;

        state.haveCongtent = false;
        
    }
}

export default  {
    state,
    getters,
    mutations,
    actions
}


