<template>
    <div class="carComponent">
        <div  @click.stop='add' class="add">+</div>
         <div class="count" v-show='food.count>0'>{{food.count}}</div>
          <div class="decrease" @click.stop='decrease'  v-show='food.count>0'>-</div>
        
    </div>
</template>

<script>
import Vue from 'vue'
export default {
    //接受传递的类的信息
    props:[
        'food'
        ],
    methods:{
        //购物车每个类中都有，他只控制当前类，需要引用购物车组件时，需要传递当前类
        add(){
            // console.log('+:'+this.food)
            if(!this.food.count){
                //给原有类添加属性
                Vue.set(this.food,'count',1);
            }else{
                this.food.count ++;
            }
        },
        decrease(){
            if(this.food.count){
                this.food.count --;
            }
        }
    }
  
}
</script>

<style lang="less" scoped>
    .carComponent{
        width:1.7rem;
        height: 0.5rem;
       
        display: flex;
        justify-content: flex-start;
        
            .add{
                width: 0.5rem;
                height: 0.5rem;
                
                line-height: 0.5rem;
                text-align: center;
                border-radius: 0.5rem;
                background-color: rgb(0,160,220);
                margin-right:0.1rem;
              
                color: white;
            }
            .count{
                width: 0.5rem;
                height: 0.5rem;
               
                line-height: 0.5rem;
                text-align: center;
                border-radius: 0.5rem;
               
                margin-right:0.1rem;
                
            }
            .decrease{
                 width: 0.5rem;
                height: 0.5rem;
                
                line-height: 0.5rem;
                text-align: center;
                border-radius: 0.5rem;
                background-color: rgb(0,160,220);
                margin-right:0.rem;
                color: white;
            }
           
        
    }

</style>





