import axios from 'axios';
import TYPES from '../types.js'

const state = {
    goods :[],
    detailShow : false,
    food : {},
    showCars:false,

    showCarDetail:false,
    
}

const getters = {
    goods(state){
        return state.goods;
    },
    detailShow(state){
        return state.detailShow;
    },
    food(state){
        return state.food;
    },
    showCars(state){
        return state.showCars;
    },
    showCarDetail(state){
        return state.showCarDetail;
    }
    
    

}

const actions = {
    getGoods({commit}){
        axios.get('data.json').then(res=>{
            commit(TYPES.GET_GOODS,res.data.goods);
            console.log(res.data.goods);
        })
    },
    showFoodDetail({commit},food){
        commit(TYPES.SHOW_FOODDETAIL,food);
    },
    hideDetail({commit}){
        commit(TYPES.HIDE_DETAIL)
    },
    showCar({commit}){
        commit(TYPES.SHOW_CAR)
    },
    CarDetail({commit}){
        commit(TYPES.CARDETAI)
    },
    hideCar({commit}){
        commit(TYPES.HIDE_CAR);
    },
    clearNum({commit}){
        commit(TYPES.CLEARNUM);
    }

   
}


const mutations = {
       [TYPES.GET_GOODS](state,data){
           state.goods = data;
        //    console.log(data);
       },
       [TYPES.SHOW_FOODDETAIL](state,food){
           state.detailShow = true;
           //本页面里的food接受传出的数据
           state.food = food;



            //  把food.ratings 的值复制给ratings
           this.state.ratings.ratings = food.ratings;
           console.log(this.state);
       },
       [TYPES.HIDE_DETAIL](state){
           state.detailShow = false;
       },
       [TYPES.SHOW_CAR](state){
           state.showCars = true;
       },
       [TYPES.CARDETAI](state){
           state.showCarDetail=true;
       },
       [TYPES.HIDE_CAR](state){
           state.showCarDetail=false;
       },
       [TYPES.CLEARNUM](state){
           console.log(1);
       }
}

export default {
    state,
    getters,
    mutations,
    actions
}


