<template>
<div class="run" id="run" ref="runs">
  <div class="ratesConent">
      <!-- 星星评价 -->
      <div class="rateStars">
          <div class="left">
              <p class="left1">3.9</p>
              <p class="left2">综合评分</p>
              <p class="left3">高于周边商家{{seller.rankRate}}%</p>
          </div>
          <div class="right">
             <div class="rightContent">
                 <div class="serives1">
                     <p class="seriveAttuide1">服务态度</p>
                     <div class="s1">
                         <myStar :score = '3.9' :size='"small"'></myStar>
                     </div>
                     <p class="seriveRatas1">3.9</p>
                 </div>
                  <div class="serives2">
                     <p class="seriveAttuide2">服务态度</p>
                     <div class="s2">
                          <myStar :score = '4.0' :size='"small"'></myStar>
                     </div>
                     <p class="seriveRatas2">4.0</p>
                 </div>
                 <div class="getTime">
                     <p class="gTime1">送达时间</p>
                     <p class="gTime2">{{seller.deliveryTime}}<span>分钟</span></p>
                 </div>

             </div>
          </div>
      </div>

      <div class="m-bg"></div>
      <!-- 按钮 -->
      <ratingSelect></ratingSelect>
      <!-- <div class="btnContent">
             单独做成组件引入
          <div class="btn">
              <div class="btnAll">
                <span class="b1">全部</span> <span class="b2">57</span>
              </div>
              <div class="btnGood">
                
                <span class="b1">满意</span> <span class="b2">47</span>
              </div>
              <div class="btnNot">
                <span class="b1">不满意</span> <span class="b2">10</span>
              </div>
          </div>
          
          <div class="onlyContent">
              <img src="../../assets/imgs/duigou.png" alt="">
              <span>只看有内容的评价</span>
          </div>
      </div> -->

      <!-- 具体评价内容，用循环-->

     
          <div class="rateDetailContent" v-for="(v,k) in selectRatings" :key="k">
                <div class="raterImg">
                    <img :src="v.avatar" alt="">
                </div>
                <div class="right">
                    <div class="raterContent">
                        <p class="raterName">{{v.username}}</p>
                        <p class="rateTime">{{v.rateTime | time}}</p>
                    </div>
                    <div class="rateStart">
                       <myStar :score="v.score" :size='"small"'></myStar><span>{{v.deliveryTime}}分钟送达</span>
                    </div>
                    <div class="ratingContent">
                        {{v.text}}
                    </div>
                    <div class="rateMenu"  >
                        <!-- <img src="../../assets/imgs/figer-on.png" alt=""> -->
                        <div class="menuList">
                            <!-- 客户喜欢或者不喜欢的菜单，也用循环 -->
                          <div class="menu" v-for="(v,i) in v.recommend" :key="i">
                           {{v}}
                          </div>

                         </div>
                    </div>
                </div>
               
          </div>




        
               
          </div>
          <div class="last"></div>

      
      
  </div>
</div>
</template>
<script>
import {mapGetters} from 'vuex'
import BScroll from 'better-scroll'
import myStar from '../star/star.vue'
import ratingSelect from '../ratingSelect/ratingSelect.vue'
import time from '../../time.js'
export default {
    computed:{
        ...mapGetters([
            'seller',
            'ratings',
            'selectRatings'
            
        ])
    },
    components:{
        myStar,
        ratingSelect
    },
     watch:{
       ratings(){
				
			}
    },
    mounted(){
            this.$nextTick( () => {
					if( !this.ratingsSCroll ){
						this.ratingsSCroll = new BScroll( document.getElementById('run') ,{
                            click:true
                        });
					}else{
						this.ratingsSCroll.refresh();
					}
				})
    }
  
}
</script>
<style lang="less" scoped>
.run{
    position: absolute;
		top: 3.6rem;
		bottom: 1rem;
        z-index: -55;
        height: 8.5rem;
        overflow: hidden;
    .ratesConent{
        width: 100%;
        
        /* 星星评价*/
        .rateStars{
          
            position: relative;
            box-sizing: border-box;
            border-bottom:0.01rem solid rgba(7,17,27,0.5);
            z-index: -5;
            .left{
                width: 2.75rem;
                height: 1.4rem;
                border-right:0.01rem solid rgba(7,17,27,0.5);
                text-align: center;
                margin:0.36rem 0 0.36rem 0;
                 box-sizing: border-box;
                .left1{
                    font-size: 0.48rem;
                    color:rgb(255,153,0);
                    line-height: 0.56rem;

                }
                .left2{
                    font-size: 0.24rem;
                    color:rgb(7,17,27);
                    line-height: 0.24rem;
                    margin-top: 0.12rem;
                }
                .left3{
                    font-size: 0.2rem;
                    color:rgb(147,153,159);
                    line-height: 0.2rem;
                    margin-top: 0.16rem;
                }
            }
            .right{
                width: 4.75rem;
                height: 1.4rem;
                position: absolute;
                top:-0.36rem;
                left:2.6rem;
                
                 margin:0.36rem 0 0.36rem 0;
                 box-sizing: border-box;
                 .rightContent{
                     margin:0 0.48rem 0 0.48rem;
                    
                     box-sizing: border-box;
                     
                     .serives1{
                         display: flex;
                        justify-content: space-between;
                         height: 0.36rem;
                         .seriveAttuide1{
                             font-size: 0.24rem;
                             color:rgb(7,17,27);
                             line-height: 0.24rem;
                             
                             
                         }
                         .s1{
                                 margin-top:-0.05rem;
                                /* border: 0.01rem solid red;*/
                             }
                         .seriveRatas1{
                             font-size: 0.24rem;
                             color:rgb(255,153,0);
                             line-height: 0.24rem;
                         }

                     }
                     .serives2{
                         display: flex;
                        justify-content: space-between;
                         height: 0.36rem;
                         margin-top: 0.16rem;
                         .seriveAttuide2{
                             font-size: 0.24rem;
                             color:rgb(7,17,27);
                             line-height: 0.24rem;
                         }
                         .s2{
                              margin-top:-0.05rem;
                         }
                         .seriveRatas2{
                             font-size: 0.24rem;
                             color:rgb(255,153,0);
                             line-height: 0.24rem;
                         }
                     }
                     .getTime{
                         display: flex;
                         justify-content: space-between;
                          margin-top: 0.16rem;
                          .gTime1{
                             font-size: 0.24rem;
                             color:rgb(7,17,27);
                             line-height: 0.36rem;
                         }
                         .gTime2,span{
                             font-size: 0.24rem;
                             color:rgb(147,153,159);
                             line-height: 0.36rem;
                         }
                     }
                 }
            }
        }
        .m-bg{
            width: 100%;
            height: 0.36rem;
            background-color: #f3f5f7;
        }
        /* 按钮*/
        .btnContent{
            border-bottom:0.01rem solid rgba(7,17,27,0.5);
            border-top:0.01rem solid rgba(7,17,27,0.5);
           
            
            .btn{
                width: 90%;
                height:0.72rem ;
                margin-left:0.36rem;
                padding:0.36rem 0 0.36rem 0;
                display: flex;
                justify-content: flex-start; 
                border-bottom:0.01rem solid rgba(7,17,27,0.5);
                .btnAll{
                    width: 1.5rem;
                    height: 0.72rem;
                    border-radius: 0.1rem;
                    background-color: rgb(0,160,220);
                    box-sizing: border-box;
                    margin-bottom: 0.36rem;
                    padding: 0.16rem 0.24rem 0.16rem 0.24rem;
                   
                    .b1{
                        font-size: 0.24rem;
                        color:rgb(255,255,255);
                        line-height: 0.4rem;
                    }
                    .b2{
                        font-size: 0.16rem;
                        color:rgb(255,255,255);
                        line-height: 0.32rem;
                        margin-left: 0.15rem;
                    }
                }
                .btnGood{
                    width: 1.5rem;
                    height: 0.72rem;
                    border-radius: 0.1rem;
                    background-color: lightblue;
                    box-sizing: border-box;
                    margin-left: 0.16rem;
                    padding: 0.16rem 0.24rem 0.16rem 0.24rem;
                   
                    .b1{
                        font-size: 0.24rem;
                        color:rgb(77,85,93);
                        line-height: 0.4rem;
                    }
                    .b2{
                        font-size: 0.16rem;
                        color:rgb(77,85,93);
                        line-height: 0.32rem;
                        margin-left: 0.15rem;
                    }
                   
                }
                .btnNot{
                    width: 1.7rem;
                    height: 0.72rem;
                    border-radius: 0.1rem;
                    background-color: lightgray;
                    box-sizing: border-box;
                    padding: 0.16rem 0.24rem 0.16rem 0.24rem;
                    margin-left: 0.16rem;
                    .b1{
                        font-size: 0.24rem;
                        color:rgb(77,85,93);
                        line-height: 0.4rem;
                    }
                    .b2{
                        font-size: 0.16rem;
                        color:rgb(77,85,93);
                        line-height: 0.32rem;
                        margin-left: 0.15rem;
                    }
                }
            }
           
            .onlyContent{
                margin:0.36rem 0 0.36rem 0.36rem;
                img{
                    width:0.4rem;
                    height: 0.4rem;
                    vertical-align:middle;
                }
                span{
                    font-size: 0.24rem;
                    color:rgb(147,153,159);
                    line-height: 0.24rem;
                     vertical-align:middle;
                }
            }
        
        }

        /* 具体评价内容*/
        .rateDetailContent{
            margin:0.36rem 0.36rem 0.36rem 0.36rem;
            border-bottom:0.01rem solid rgba(7,17,27,0.5);
            
            display: flex;
            justify-content: space-between;
            .raterImg{
                img{
                    width: 0.56rem;
                    height: 0.56rem;
                }
            }
            .right{
                width:89%;
               margin-bottom: 0.36rem;
                
                .raterContent{
                    display: flex;
                    justify-content: space-between;
                    .raterName{
                        font-size: 0.2rem;
                        color: rgb(7,17,27);
                        line-height: 0.24rem;
                    }
                    .rateTime{
                        font-size: 0.2rem;
                        font-weight: 200;
                        color: rgb(147,163,159);
                        line-height: 0.24rem;
                    }

                }
                .rateStart{
                    display: flex;
                    justify-content: flex-start;
                    margin-top:0.08rem;
                    span{
                        font-size: 0.2rem;
                        font-weight: 200;
                        color:rgb(147,163,159);
                        line-height: 0.24rem;
                        margin-left:0.12rem;
                        margin-top:0.05rem;
                    }
                }
                .ratingContent{
                    width:6.2rem;
                    margin-top: 0.12rem;
                    font-size: 0.24rem;
                    color:rgb(7,17,27);
                    line-height: 0.36rem;
                }
                .rateMenu{
                     width:5.5rem;
                    margin-top:0.16rem;
                    display: flex;
                    justify-content: flex-start;
                   
                   
                    img{
                        width: 0.24rem;
                        height: 0.24rem;
                        color:rgb(0,160,220);
                        line-height: 0.32rem;
                    }
                    .menuList{
                        display: flex;
                        justify-content: flex-start;
                         flex-wrap: wrap;
                         margin-top:-0.1rem;
                        .menu{
                        width: 1.2rem;
                        height:0.3rem;
                        border:0.01rem solid rgba(7,17,27,0.1);
                        border-radius: 0.02rem;
                        font-size: 0.18rem;
                        color: rgb(147,153,159);
                        line-height: 0.32rem;
                        padding: 0.06rem 0.12rem 0.06rem 0.12rem;
                        margin-left:0.16rem;
                        margin-top:0.05rem;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        }
                    }
                }
            }
                
           
        }
        .last{
            height: 0.01rem;
        }
    } 
}  

</style>





