<template>
    <div class="header">
        
        <img :src="seller.avatar" alt="" id="bg">
        <div class="topHeader">
            <div class="headerLeft" @click="showDetail">
                <img :src="seller.avatar" alt="">
            </div>
            <div class="headerRight"> 
                <div class="titl">
                    <img src="../../assets/imgs/brand.png" alt="">
                    <p> {{seller.name}}</p>
                </div>
                <div class="titl-a">{{seller.description}}／{{seller.deliveryTime}}分钟到达</div>
                <div class="titl-b">
                    <img src="../../assets/imgs/decrease.png" alt="">
                    <p v-if="seller.supports">{{seller.supports[0].description}}</p>
                </div>
                <div class="titl-c"> 5个 &nbsp;> </div>
            </div>
        </div>
        <div class="bottomHeader">
            <img src="../../assets/imgs/bulletin.png" alt="">
            <p>{{seller.bulletin}}</p>
            <p class="b1">></p>

        </div>
        
        <sellerDetail></sellerDetail>
       
    </div>
  
</template>
<script>
    import {mapGetters} from 'vuex';
    import sellerDetail from '../sellerDetail/sellerDetail.vue'
export default {
    computed:{
        ...mapGetters([
                'seller'
        ])
    },
    components:{
        sellerDetail
    },
    methods:{
            //点击图片跳到sellerDetail页面
            showDetail(){
                console.log(1)
                this.$store.dispatch("showDetail")
            }
    }
  
}
</script>
<style lang="less" scoped>
    .header{
        width: 100%;
        height: 2.68rem;
        box-sizing: border-box;
        overflow: hidden;
        position: relative;
           
        #bg{
           width: 100%;
           height: 100%;
            position:absolute;
            left: 0;
            top:0;
            right: 0;
            bottom:0;
            z-index:-5;
            filter:blur(5px);
        }
        .topHeader{
            width:100%;
            height:2.12rem;
            background:rgba(7,17,27,0.5);
            .headerLeft{
                
              
                position:absolute;
                
                img{
                    width: 1.28rem;
                    height:1.28rem;
                    
                    margin:0.48rem 0 0.36rem 0.48rem; 
                    
                }
            }
            
            .headerRight{
                width:65%;
                height:1.28rem;
                margin:0.48rem 0.24rem 0.36rem 2.08rem;
              
                position:absolute;
                .titl{
                    position:relative;
                    img{
                    
                        width:0.62rem;
                        height:0.37rem;
                        margin-top:0.04rem;
                        
                    }
                    p{
                        position: absolute;
                        margin:-0.37rem 0 0 0.7rem;
                        font-size: 0.32rem;
                        color:rgb(255,255,255);
                        font-weight:bold;
                        line-height:0.36rem;

                    }  
                }
                .titl-a{
                    margin-top:0.16rem;
                    font-size: 0.24rem;
                    color:rgb(255,255,255);
                    font-weight:200;
                    line-height:0.24rem;
                }
                .titl-b{
                        margin-top:0.2rem;
                        position:relative;
                    img{
                        margin-top:-0.1rem;
                        width:0.25rem;
                        height:0.25rem;
                    }
                    p{
                        position: absolute;
                        margin:-0.28rem 0 0 0.32rem;
                        font-size:0.2rem;
                        color:rgb(255,255,255);
                        font-weight:200;
                        line-height: 0.24rem;  
                    }
                }
                .titl-c{
                    width:0.8rem;
                    height:0.5rem;
                    font-size: 0.2rem;
                    color:rgb(255,255,255);
                    font-weight: 200;
                    line-height: 0.5rem;
                    border-radius: 1rem;
                    position: absolute;
                    top:0.8rem;
                    right:0rem;
                    padding-left: 0.16rem;
                    background:rgba(7,17,27,0.45);
                  
                }

            }
         }
        .bottomHeader{
            width:100%;
            height:0.56rem;
            background:rgba(7,17,27,0.7);
            display: flex;
            flex-wrap: nowrap;
           
            align-items: center;
            img{
                width:0.45rem;
                height:0.28rem;
                margin-left:0.24rem;
                
            }
            p{
                width: 6.25rem;
                height:0.2rem;
               
                font-size: 0.2rem;
                color:rgb(255,255,255);
                font-weight: 200;
                line-height: 0.2rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                margin-left:0.08rem;
            }
            .b1{
                width:0.2rem;
                height:0.2rem;
                margin-left:0.08rem;
            }
        }
       
    }

</style>





